import { useState } from "react";
import { Box, Typography, Divider, Dialog, Button } from "@mui/material";

export default function Transactions({ data }) {
  const [selectedTxn, setSelectedTxn] = useState(null);

  return (
    <Box
      sx={{
        width: "100%",
        bgcolor: "var(--bs-primary1)",
        p: 1,
        borderRadius: 2,
        border: "1px solid var(--bs-primary2)",
        color: "var(--bs-primary2)",
      }}
    >
      {data.slice(0, 5).map((txn, index) => (
        <Box key={txn._id}>
          <Box
            onClick={() => setSelectedTxn(txn)}
            sx={{
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
              px: 1,
              py: 1.4,
              gap: 1,
              flexWrap: "nowrap",
              cursor: "pointer",
              "&:hover": {
                bgcolor: "var(--bs-primary3)",
              },
            }}
          >
            {/* LEFT SIDE */}
            <Box sx={{ flex: 1, minWidth: 0, overflow: "hidden" }}>
              <Typography
                sx={{
                  fontSize: "14px",
                  fontWeight: 600,
                  color: "var(--bs-primary2)",
                  whiteSpace: "nowrap",
                  overflow: "hidden",
                  textOverflow: "ellipsis",
                }}
              >
                {txn.message}
              </Typography>

              <Typography
                sx={{
                  fontSize: "12px",
                  color: "var(--bs-primary2)",
                  opacity: 0.7,
                  whiteSpace: "nowrap",
                }}
              >
                {txn.createdAt}
              </Typography>
            </Box>

            {/* Amount */}
            <Typography
              sx={{
                fontWeight: 700,
                fontSize: "15px",
                whiteSpace: "nowrap",
                color: txn.type === "credit" ? "green" : "red",
              }}
            >
              {txn.type === "credit" ? `+ ${txn.amount}` : `- ${txn.amount}`}
            </Typography>
          </Box>

          {index < 4 && (
            <Divider
              sx={{ opacity: 0.3, my: 0.5, borderColor: "var(--bs-primary2)" }}
            />
          )}
        </Box>
      ))}

      {/* DIALOG */}
      <Dialog
        open={Boolean(selectedTxn)}
        onClose={() => setSelectedTxn(null)}
        sx={{
          "& .MuiPaper-root": {
            width: "380px",
            borderRadius: 4,
            p: 3,
            bgcolor: "var(--bs-primary1)",
            color: "var(--bs-primary2)",
            border: "1px solid var(--bs-primary2)",
          },
        }}
      >
        {selectedTxn && (
          <Box>
            
            <Typography
              sx={{
                fontSize: "18px",
                fontWeight: 700,
                textAlign: "center",
                mb: 1,
                color: "var(--bs-primary2)",
              }}
            >
              Transaction Details
            </Typography>

            <Divider sx={{ my: 2, borderColor: "var(--bs-primary2)" }} />

            <Typography sx={{ fontSize: "16px", fontWeight: 600, mb: 2 }}>
              {selectedTxn.message}
            </Typography>

            <Box
              sx={{
                bgcolor:
                  selectedTxn.type === "credit"
                    ? "rgba(0,255,0,0.08)"
                    : "rgba(255,0,0,0.08)",
                p: 2,
                borderRadius: 3,
                textAlign: "center",
                mb: 2,
              }}
            >
              <Typography
                sx={{
                  fontSize: "22px",
                  fontWeight: 700,
                  color: selectedTxn.type === "credit" ? "green" : "red",
                }}
              >
                {selectedTxn.type === "credit"
                  ? `+ ${selectedTxn.amount}`
                  : `- ${selectedTxn.amount}`}
              </Typography>
            </Box>

            {/* DETAILS */}
            <Box sx={{ display: "grid", gap: 1.5 }}>
              {[
                ["Transaction Type", selectedTxn.trans_type],
                ["Date", selectedTxn.createdAt],
                ["Old Balance", `${selectedTxn.o_bal}`],
                ["New Balance", `${selectedTxn.c_bal}`],
                ["Txn ID", selectedTxn.txn_id],
                ["Order ID", selectedTxn.order_id],
              ].map(([label, value]) => (
                <Box key={label}>
                  <Typography sx={{ fontSize: "12px", color: "var(--bs-primary2)", opacity: 0.7 }}>
                    {label}
                  </Typography>
                  <Typography sx={{ fontWeight: 600, wordBreak: "break-all" }}>
                    {value}
                  </Typography>
                </Box>
              ))}
            </Box>

            <Divider sx={{ my: 3, borderColor: "var(--bs-primary2)" }} />
          </Box>
        )}
      </Dialog>
    </Box>

  );
}
